<template>
	<div class="container" v-if="init">
		<div>{{source.article.title}}</div>
		<div class="comment-list">
			<sub-comment 
			v-for="comment in dataComments()"
			:comment="comment"
			:key="comment.id"
			@commentDestroy="commentDestroy"
			></sub-comment>
		</div>
		<a href="javascript:void(0)" @click="commentCreateFlag(forms.comment.create,source.article.id)">添加</a>
		<comment-sub-create 
		:form="forms.comment.create"
		@commentCreate="commentCreate"
		></comment-sub-create>
	</div>
</template>

<script type="text/javascript">
import Article from '@/service/Article.js'
import Comment from '@/service/Comment.js'
import SubComment from '@/components/comment/sub/One.vue'
import CommentSubCreate from '@/components/comment/sub/Create.vue'
import ErrorHandler from '@/helpers/ErrorHandler.js'

var ret={
	data:function(){
		return {
			init:false,
			source:null,
			forms:{
				comment:{
					create:Comment.createForm("create")
				},
			}
		};
	},
	components:{
		'sub-comment':SubComment,
		'comment-sub-create':CommentSubCreate
	},
	beforeRouteEnter:function(to,from,next){
		Article.comments({id:to.params.id},function(response){
			next(function(vm){
				vm.source=response.data;
				vm.init=true;
			});
		},function(error){
			ErrorHandler.handleGet(error,to,from,next);
		});
	},
	methods:{
		dataComments:function(){
			return this.source.comments;
		}
	}
};
Comment.attach(ret);

export default ret;
</script>


